import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Alert from '@/components/alert'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello,
      meta: { isfade: true }//transition
    },
    {
      path: '/alert',
      name: 'Alert',
      component: Alert,
      meta: { isfade: true }
    }
  ]
})



